/* carousel.css */
.slick-slider {
    position: relative;
    /* margin: 20px auto; */
    /* max-width: 500px; */
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Slides */
.slick-slide img {
    width: 100%;
    height: auto;
    border-radius: 12px;
    object-fit: cover;
}

/* Dots */
.slick-dots {
    bottom: 10px;
}

.slick-dots li button:before {
    font-size: 12px;
    color: #ccc;
    opacity: 0.8;
}

.slick-dots li.slick-active button:before {
    color: #000;
    opacity: 1;
}

.image-banner-container {
    background-image: url(../../assets/Banner.png);
    background-size: cover;
    background-position: center;
    height: 300px;
    width: 100%;
    border-radius: 12px;

}

/* Arrows */
.slick-prev,
.slick-next {
    width: 35px;
    height: 35px;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
}

.slick-prev:before,
.slick-next:before {
    font-size: 20px;
    color: #333;
    opacity: 1;
    padding-top: 2px;
}

.slick-prev {
    left: 10px;
}

.slick-next {
    right: 10px;
}