/* // container css */
.container {
    width: 80%;
    max-width: 100%;
    margin: auto;
}

@media (max-widht:1200px) {
    .container {
        width: 90%;
    }

}

@media (max-width:768px) {
    .container {
        width: 92%;
    }
}

@media (max-width:500px) {
    .container {
        width: 96%;
    }
}

