/* // flex csss */
.inline {
    display: flex;
}

.inline-between {
    display: flex;
    justify-content: space-between;
}

.inline-start {
    display: flex;
    justify-content: flex-start;
}

.inline-evenly {
    display: flex;
    justify-content: space-evenly;
}

.inline-around {
    display: flex;
    justify-content: space-around;
}

.inline-center {
    display: flex;
    justify-content: center;
}

.text-center {
    text-align: center;
}

.align-center {
    align-items: center;
}

